body {
  font: 18px "Century Gothic", Futura, sans-serif; /* size, font */
  margin: 20px; /* space outside of border */
}

form {
  margin-bottom: 20px;
}

ol,
ul {
  padding-left: 30px; /* space b/t left border to content */
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: rgb(255, 255, 255);
  border: 1px solid #999;
  float: left;
  font-size: 60px;
  font-weight: bold;
  line-height: 80px;
  height: 80px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center; /* text in the middle */
  width: 80px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
